<template>
  <div class="propertyPreBill">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
    </navigation-bar>
    <div class="propertyPreBillContent" :class="{tabThemeL:bindThemeL}">
      <van-tabs :swipe-threshold="2" @click="onTabChanged">
        <van-tab
          v-for="(room,roomIndex) in roomList"
          :key="roomIndex"
          :title="room.roomName"
          :ellipsis="false"
        ></van-tab>
      </van-tabs>
      <div class="card" @click="showMonths()">
        <div class="list">
          <div class="item" :class="{itemPreThemeL:bindThemeL}">
            预缴周期
            <div class="item-note" style="color:black;display: flex;">
              <div class="selectMonth">{{months}}个月</div>
              <van-image width="14" height="14" style="margin-top: 3px" :src="arrowRightImg" />
            </div>
          </div>
        </div>
      </div>
      <div class="card">
        <div class="list">
          <div class="item" :class="{itemOtherThemeL:bindThemeL}">
            <div style="font-weight:bolder">固定费用项目</div>
          </div>
          <div class="item" :class="{subItemThemeL:bindThemeL}" style="padding-top:0px">
            <div
              v-for="(item,cycleIndex) in cycleOptionList"
              :key="cycleIndex"
              style="vertical-align: center;margin-top: 16px;height: 20px;display: flex;"
            >
              <div style="display: flex;width: 50%">
                <div style="height: 20px;">{{item.chargeItem}}</div>
                <div v-if="item.discount<100" class="saleTag">{{item.discount/10}}折</div>
              </div>

              <div
                class="item-note"
                style="height: 20px;display: flex;width: 50%;justify-content: flex-end"
              >
                <div
                  class="fs12"
                  v-if="item.discount<100"
                  style="height: 20px;margin-right:4px;text-decoration:line-through;"
                >￥{{item.advanceMoney}}</div>
                <div
                  class="fs16"
                  v-if="useDiscount==1"
                  style="height: 20px;color:black"
                >￥{{item.chargeMoney}}</div>
                <div
                  class="fs16"
                  v-if="useDiscount==0"
                  style="height: 20px;color:black"
                >￥{{item.advanceMoney}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="card" v-if="noCycleOptionList.length>0">
        <div class="list">
          <div class="item" :class="{itemOtherThemeL:bindThemeL}">
            <div style="font-weight:bolder">非固定费用项</div>
          </div>

          <div class="item" :class="{subItemThemeL:bindThemeL}" style="padding-top:0px;">
            <div class="pt16" v-for="(item,nocycleIndex) in noCycleOptionList" :key="nocycleIndex">
              <span style="height: 22px;">{{item.chargeItem}}</span>
              <span class="item-note">
                <div class="change_point">
                  <van-image class="delete_icon" :src="cutImg" @click="numberReduce(item)" />
                  <input
                    id="electric"
                    type="number"
                    class="point_num"
                    v-model="item.advanceMoney"
                    @change="noCycleChanged(item)"
                  />
                  <van-image class="add_icon" :src="addImg" @click="numberAdd(item)" />
                </div>
              </span>
            </div>
          </div>
        </div>
      </div>

      <div class="card" v-if="hasDiscount">
        <div class="list">
          <div class="item" :class="{itemOtherThemeL:bindThemeL}">
            使用物业折扣
            <div class="item-note">
              <van-image
                class="choose_is_point"
                :src="useDiscount?switchOnImg:switchOffImg"
                @click="useDiscountChanged()"
              />
            </div>
          </div>
        </div>
      </div>

      <div class="card pb25 pt10">
        <div style="display: flex;">
          <div class="amountTitleGray pt16" :class="{amountThemeL:bindThemeL}">订单总额</div>
          <div class="amountValueBlack pt16" :class="{amountThemeL:bindThemeL}" align="right">￥{{amount}}</div>
        </div>
        <div style="display: flex;" v-if="hasDiscount">
          <div class="amountTitleGray pt16">物业折扣</div>
          <div class="amountValueBlack pt16">-￥{{discount}}</div>
        </div>
      </div>
    </div>

    <property-bill-amount
      :amount="amount"
      :isAllCheck="false"
      @checkAllFn="allCheckFn()"
      @createOrderPay="createPreOrder()"
      :isShowCheck="false"
    ></property-bill-amount>

    <!-- 月份选择弹框 -->
    <van-popup
      v-model="showMonthSelect"
      @click-overlay="selectMonthCancel"
      position="bottom"
      :style="{ height: '36%' }"
    >
      <div class="areaselect-topbar">
        <div @click="selectMonthCancel">取消</div>
        <span @click="selectMonthOk">确定</span>
      </div>
      <van-picker :columns="monthSelect" :default-index="selectMonthIndex" @change="onMonthChange" />
    </van-popup>
  </div>
</template>

<script>
import {
  getRoomListService,
  getPreBillListService,
  createPreOrderService
} from "../../../services/propertyBillService";
import propertyBillAmount from "./propertyBillAmount";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "propertyPreBill",
  data() {
    return {
      roomList: [], //绑定的房间
      months: 6, //预缴月数
      amount: 0, //物业费
      discount: 0, //折扣
      hasDiscount: false, //是否有物业折扣
      useDiscount: 0, //使用全部折扣标识
      cycleOptionList: [], //周期性费用
      noCycleOptionList: [], //非周期性费用
      curRoomCode: "", //当前房间编码
      totalMoney: "",
      monthSelect: ["3个月", "6个月", "9个月", "12个月", "15个月", "18个月"],
      monthList: [3, 6, 9, 12, 15, 18],
      selectMonthIndex: 1,
      showMonthSelect: false,
      headerItemWidth:{
          width:'84px'
      },
      title:'预缴费',
      switchOnImg: require("../../../assets/images/home/property/payment/switch_on.png"),
      switchOffImg: require("../../../assets/images/home/property/payment/switch_off.png"),
      defalutImg: require("../../../assets/images/home/property/payment/img_default@2x.png"),
      addImg: require("../../../assets/images/home/property/advancePayment/bg_add.png"),
      cutImg: require("../../../assets/images/home/property/advancePayment/bg_cut.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  components: {
    NavigationBar,
    "property-bill-amount": propertyBillAmount
  },
  mounted() {
    window.addEventListener("native.keyboardhide", e => {
      this.validPoint();
    });

    this.getRoomList();
  },
  methods: {
    getRoomList() {
      let reqData = {
        areaCode: this.$route.params.areaCode,
        userCode: this.$userInfo.OWNER_CODE
      };

      getRoomListService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == "200") {
            console.log(JSON.stringify(result.data.roomList));
            this.roomList = result.data.roomList;
            this.curRoomCode = this.roomList[0].roomCode;
            this.preBillList();
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    preBillList() {
      let reqData = { roomCode: this.curRoomCode, months: this.months };
      getPreBillListService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == 200) {
            this.cycleOptionList = result.data.cycleOptionList;
            this.noCycleOptionList = result.data.noCycleOptionList;
            for (var i = 0; i < this.noCycleOptionList.length; i++) {
              if (this.noCycleOptionList[i].advanceMoney == "")
                this.noCycleOptionList[i].advanceMoney = 0;
            }
            this.calculateAmount();
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    useDiscountChanged() {
      this.useDiscount = this.useDiscount == 1 ? 0 : 1;
      this.calculateAmount();
    },
    //房间tab点击
    onTabChanged(index) {
      this.curRoomCode = this.roomList[index].roomCode;
      this.cycleOptionList = [];
      this.noCycleOptionList = [];
      this.amount = 0;
      this.useDiscount = 0;
      this.discount = 0;
      this.totalMoney = "";
      this.preBillList();
    },
    //计算物业费总计
    calculateAmount() {
      //订单总额
      var aPrice = 0.0;
      var aCount = 0.0;

      for (var i = 0; i < this.cycleOptionList.length; i++) {
        aPrice += parseFloat(this.cycleOptionList[i].advanceMoney);
        if (
          this.cycleOptionList[i].discountMoney != "" &&
          this.useDiscount == 1
        ) {
          aCount += parseFloat(this.cycleOptionList[i].discountMoney);
          this.hasDiscount = true;
        }
      }
      for (var i = 0; i < this.noCycleOptionList.length; i++) {
        if (!this.noCycleOptionList[i].advanceMoney == "")
          aPrice += parseFloat(this.noCycleOptionList[i].advanceMoney);
      }

      this.amount = parseFloat(aPrice).toFixed(2);
      this.discount = parseFloat(aCount).toFixed(2);

      this.totalMoney = parseFloat(this.amount - this.discount).toFixed(2);
    },
    //水电费计算  type:0=电费 1=水费
    //按钮加
    numberAdd(item) {
      if (item.advanceMoney == 50 || item.advanceMoney > 50) {
        item.advanceMoney = parseInt(item.advanceMoney) + 50;
        item.chargeMoney = item.advanceMoney;
      } else {
        item.advanceMoney = 50;
        item.chargeMoney = item.advanceMoney;
      }

      this.calculateAmount();
    },
    numberReduce(item) {
      if (item.advanceMoney == 50 || item.advanceMoney > 50) {
        item.advanceMoney -= 50;
        item.chargeMoney = item.advanceMoney;
      } else {
        item.advanceMoney = 0;
        item.chargeMoney = item.advanceMoney;
      }

      this.calculateAmount();
    },
    //非固定费用项发生改变
    noCycleChanged(item) {
      var money = item.advanceMoney;
      if (money == undefined || money == null) {
        this.$toast("请输入正确金额");
        item.advanceMoney = 0;
        item.chargeMoney = item.advanceMoney;
        this.calculateAmount();
        return;
      }
      if (money > 9999.99) {
        this.$toast("最大缴费金额不能超过 9999.99，请重新输入");
        item.advanceMoney = 0;
        item.chargeMoney = item.advanceMoney;
        this.calculateAmount();
        return;
      }
      if (money % 50 != 0) {
        this.$toast("金额必须是50的整数倍！");
        if (money < 50) {
          money = 50;
        } else {
          money = money - (money % 50);
        }
        item.advanceMoney = money;
        this.calculateAmount();
        return;
      }
      item.chargeMoney = item.advanceMoney;
      this.calculateAmount();
    },
    validPoint(){
      for(let i=0;i<this.noCycleOptionList.length;i++){
        this.noCycleChanged(this.noCycleOptionList[i]);
      }
    },
    //创建缴费订单
    createPreOrder() {
      let reqData = {
        userCode: this.$userInfo.OWNER_CODE,
        roomCode: this.curRoomCode,
        months: this.months,
        useDiscount: this.useDiscount,
        totalMny: this.totalMoney,
        cycleOptionList: this.cycleOptionList,
        noCycleOptionList: this.noCycleOptionList
      };
      createPreOrderService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == 200) {
            this.$router.push({
              name: "propertyPreOrderPay",
              params: {
                billNo: result.data
              }
            });
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 月份选择相关 */
    showMonths() {
      this.showMonthSelect = true;
    },
    selectMonthCancel() {
      this.showMonthSelect = false;
    },
    selectMonthOk() {
      this.showMonthSelect = false;
      this.months = this.monthList[this.selectMonthIndex];
      this.preBillList(this.roomList[0].roomCode);
    },
    onMonthChange(picker, value, index) {
      this.selectMonthIndex = index;
    },
    onClickLeft() {
      this.$router.back();
    }
  }
};
</script>
<style scoped lang="scss">
.propertyPreBill {
  background-color: #f9f9f9;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.propertyPreBillContent{
  flex: 1;
  overflow-y: scroll;
}
.propertyPreBillContent::-webkit-scrollbar {
  display: none;
}
/deep/.tabThemeL .van-tab__text {
  font-size: 17px !important;
}
/deep/ .van-tabs__line {
  box-shadow: 0 -1px 5px 0 rgba(165, 197, 165, 0.39);
  width: 20px !important;
  height: 3px !important;
  background-color: #17B7BD !important;
}

.card {
  margin: 20px 15px;
  border-radius: 3px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  background-color: #fff;
}
.item-note {
  float: right;
  font-size: 14px;
  color: #333333;
  text-align: right;
  /* margin-right: 16px; */
}
.list {
  position: relative;
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 0;
  margin-bottom: 20px;
}
.item {
  border-color: #ddd;
  background-color: #fff;
  color: #444;
  position: relative;
  z-index: 2;
  display: block;
  margin: -1px;
  padding: 16px;
  border-width: 1px;
  border-style: solid;
  font-size: 16px;
}
/*预缴周期*/
.itemPreThemeL {
  font-size: 17px;
}
.itemPreThemeL .item-note {
  font-size: 17px;
}
/*固定费用项目，非固定费用项目*/
.itemOtherThemeL {
  font-size: 20px;
}
.subItemThemeL {
  font-size: 17px;
}
.subItemThemeL .item-note {
  font-size: 17px;
}
.choose_is_point {
  float: right;
  width: 59px;
  height: 32px;
}

/* 订单总额+物业折扣 */
.amountTitleGray {
  width: 50%;
  font-size: 16px;
  color: #9a9a9a;
  margin-left: 16px;
}
.amountValueBlack {
  margin-right: 16px;
  font-size: 16px;
  width: 50%;
  text-align: right;
}
.amountThemeL {
  font-size: 17px;
}
.pb25 {
  padding-bottom: 25px;
}
.pt10 {
  padding-top: 10px;
}
.pt16 {
  padding-top: 16px;
}
/* 水电费 */
.propertyPreBill .change_point {
  overflow: hidden;
  width: 120px;
  float: right;
  height: 26px;
  background: #ffffff;
  display: flex;
  position: relative;
}
.propertyPreBill .add_icon {
  float: right;
  /* height: 22px;
  width: 22px; */
  color: #9a9a9a;
  font-size: 22px;
  text-align: center;
  /* right: 16px; */
  padding: 3px;
}

.propertyPreBill .delete_icon {
  float: left;
  /* height: 22px;
  width: 22px; */
  color: #9a9a9a;
  font-size: 22px;
  text-align: center;
  padding: 3px;
}

.propertyPreBill .point_num {
  width: 60px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: black;
  background-color: #ececec;
  text-align: center;
  border-radius: 2px;
  height: 26px;
  line-height: 20px;
  padding: 0px;
  margin: 0 5px;
  border: none;
}
/* 月份选择弹框 */
.van-popup--bottom {
  overflow: hidden;
}
.areaselect-topbar {
  display: flex;
  justify-content: space-between;
  height: 44px;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  text-align: center;
  align-items: center;
  div {
    color: #3a3a3a;
    width: 60px;
    height: 44px;
    line-height: 44px;
  }
  span {
    color: #17B7BD;
    width: 60px;
    height: 44px;
    line-height: 44px;
  }
}
</style>
